import React from 'react';
import PropTypes from 'prop-types';
import { Rating} from '@material-ui/lab';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core';
import { Link } from 'react-router-dom';

const useStyles =makeStyles(({spacing,palette})=>({
    square:{
        height:spacing(10),
        width:spacing(10),
        borderRadius:spacing(1),
        margin:spacing('auto',2,'auto','auto')
    },
    root:{
        '&:hover':{
            backgroundColor:palette.background.default
        }
    }
}))

const ListViewCard = props => {
    const {imageUrl,primaryText,secondaryText,rating,id} = props;
    const classes = useStyles()
    return (
        <div>
            <ListItem
             component={Link}
             to={`/course/${id}`}
             className={classes.root}
             >
            <ListItemAvatar>
                <Avatar variant="square" className={classes.square} src={imageUrl}  />
            </ListItemAvatar>
                <ListItemText
                primary={primaryText}
                secondary={
                    <Grid
                        container
                        // spacing={6}
                        direction="column"
                        justify="center"
                        alignItems="flex-start"
                        wrap="nowrap"
                        
                    >
                        <Typography variant="subtitle2">
                            {secondaryText}
                        </Typography>
                        <Rating readOnly size="small" defaultValue={rating} />

                    </Grid>
                }
                />
            </ListItem>
        </div>
    )
}

ListViewCard.propTypes = {
    imageUrl:PropTypes.string,
    secondaryText:PropTypes.string,
    rating:PropTypes.number,
    primaryText:PropTypes.string,
    id:PropTypes.number
}
ListViewCard.defaultProps={
    imageUrl:'/img/gallary/child-865116_1920.jpg',
    primaryText:'primary text',
    secondaryText:'secondaryText',
    rating:3,
    id:1
}

export default ListViewCard
