import React from 'react';
import { Typography, Paper, TextField, Box, Button, makeStyles, Grid } from '@material-ui/core';
import { Link } from 'react-router-dom';

const useStyles = makeStyles(({palette,spacing})=>({
    root:{
        margin:spacing('auto',3),
        borderRadius:spacing(2)
    },
    contained:{
        borderRadius:spacing(4),
        padding:spacing(1.5,0),
        textTransform:'none'
    },
    text:{
        textTransform:'none',
        textDecoration:'underline',
        fontWeight:600,
        '&.on-primary':{
            color:palette.primary.contrastText
        }
        
    },
    bg:{
        backgroundColor:palette.primary.main,
        color:palette.primary.contrastText
    },
    body2:{
        color:palette.primary.contrastText,
        '&:hover':{
            textDecoration:'underline'
        }
    }
}))

const LoginPage = () => {
    
    const classes = useStyles()
    return (
        <div className={classes.bg}>
            <Box pb={6}>
                <Box py={6}>
                    <Typography variant="h4" align="center">
                        ProLearn
                    </Typography>
                </Box>
                <Paper elevation={3} className={classes.root}> 
                    <Box py={8} px={3}>

                        <Typography variant="h4" align="center" gutterBottom>
                            Welcome back
                        </Typography>
                        <Typography variant="body1" align="center">
                            Login to your account
                        </Typography>
                        <Box my={3}>
                            <form noValidate autoComplete="off">
                                <TextField
                                type='email'
                                label="EMAIL"
                                id="email"  
                                fullWidth
                                margin="normal"
                                />
                                <TextField
                                type='password'
                                label="PASSWORD"
                                id="password"  
                                fullWidth
                                />
                            </form>
                        </Box>
                        <Box my={2}>
                            <Grid
                            container
                            spacing={1}
                            direction="row"
                            justify="center"
                            alignItems="center"
                            alignContent="center"
                            
                            >
                                <Button component={Link} to="/forgetpassword" variant="text" className={classes.text} color="default">
                                    Forgot your password?
                                </Button>
                            
                            </Grid>
                        </Box>
                        <Box px={2}>
                            <Button variant="contained" component={Link} to="/feed" color="primary" className={classes.contained} fullWidth>
                                Login to your account
                            </Button>
                        </Box>
                    </Box>
                </Paper>
                <Box my={2}>
                    <Grid
                    container
                    spacing={1}
                    direction="column"
                    justify="center"
                    alignItems="center"
                    alignContent="center"
                    
                    >
                        <Typography variant="body2" className={classes.body2}>
                            Don't have an account?
                        </Typography>
                        <Button component={Link} to="/register" variant="text" className={`${classes.text} ${"on-primary"}`}>
                            Register here
                        </Button>
                    
                    </Grid>
                </Box>
            </Box>
        </div>
    )
}

export default LoginPage