import React from 'react';
import { makeStyles, Box, Grid, Typography, Button } from '@material-ui/core';
import { Link } from 'react-router-dom';

const useStyles = makeStyles(({palette,spacing})=>({
    root:{
        height:'90vh',
        flexGrow:1,
        backgroundImage:'linear-gradient(45deg, rgba(255, 255, 255, 0.9) 40%, rgba(255, 255, 255, 0)),url(/img/gallary/books-768426_1920.jpg)',
        backgroundPosition:'center',
        backgroundSize:'cover',
        MozBackgroundSize:'cover',
        WebkitBackgroundSize:'cover',
        backgroundRepeat:'no-repeat'
    },
    contained:{
        borderRadius:spacing(3),
        padding:spacing(1,0)
    },
    boxBottom:{
        paddingTop:'40vh'
    },
    h5:{
        fontSize:spacing(4)
    },
    h6:{
        textTransform:'none',
        fontWeight:700
    }
}))

export default function WelcomePage() {
    const classes = useStyles()
    return (
        <Box className={classes.root} px={3}>
            <Box className={classes.boxBottom}>
            <Grid
                container
                direction="column"
                justify="flex-end"
                alignItems="stretch"
                >
                    <Typography variant="h6" color="primary" gutterBottom className={classes.h6}>
                        ProLearn
                    </Typography>
                    <Typography variant="h5" gutterBottom className={classes.h5}>
                        Develop valuable skils anywhere,anytime.
                    </Typography>
                    <Button component={Link} to='/login' fullWidth variant="contained" color="primary" className={classes.contained}>
                        Start learning now
                    </Button>
                </Grid>
            </Box>
        </Box>
    )
}
