import React from 'react';
// import logo from './logo.svg';
// import './App.css';
import {
  Switch,
  Route
} from "react-router-dom";
import {
  PrimarySearchAppBar,
  SecondaryAppBar
} from './Components/Layouts';
import { 
  ProfilePage,
  CourseDetailPageNotPaid,
  SearchPage,
  FeedPage,
  ForgetPasswordPage,
  LoginPage,
  RegisterPage,
  WelcomePage,
  ChangePasswordPage,
 } from './Components/Pages';


function App() {
  return (
    <div>
      <Switch>
          <Route path="/course/:id">
            <SecondaryAppBar title="Course" />
            <CourseDetailPageNotPaid/>
          </Route>
          <Route path="/profile">
            <SecondaryAppBar title="Profile" />
            <ProfilePage/>
          </Route>
          <Route path="/changepassword">
            <ChangePasswordPage/>
          </Route>
          <Route path="/forgetpassword">
            <ForgetPasswordPage/>
          </Route>
          <Route path="/login">
            <LoginPage/>
          </Route>
          <Route path="/search">
            <PrimarySearchAppBar title="Search" />
            <SearchPage/>
          </Route>
          <Route path="/register">
            <RegisterPage/>
          </Route>
          <Route path="/feed">
            <PrimarySearchAppBar title="Home" />
            <FeedPage/>
          </Route>
          <Route path="/">
            <WelcomePage/>
          </Route>
      </Switch>
    </div>
  );
}

export default App;

