import React from 'react'
import PropTypes from 'prop-types'
import { Paper, Box, Grid, Typography, LinearProgress } from '@material-ui/core'
import { withStyles,makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles(({palette,spacing})=>({
    root:{
        borderRadius:spacing(2)
    }
}))

const BorderLinearProgress = withStyles(({palette}) => ({
    root: {
      height: 10,
      borderRadius: 5,
    },
    colorPrimary: {
      backgroundColor: palette.grey[palette.type === 'light' ? 400 : 700],
    },
    bar: {
      borderRadius: 5,
      backgroundColor: palette.primary.main,
    },
  }))(LinearProgress);

function ProgressBarCard(props) {
    const {current,total} = props;
    const classes = useStyles()
    return (
        <Paper elevation={10} className={classes.root}>
            <Box p={5}>
                <Grid container spacing={1}>
                  <Grid container
                  direction="row"
                  justify="space-between"
                  alignItems="center"
                  item>
                    <Typography variant="subtitle2">
                        {`${current} of ${total} lessons completed`}
                    </Typography>
                    <Typography variant="subtitle2">
                        {`${Math.floor(current/total*100)}%`}
                    </Typography>
                  </Grid>
                  {/* grid 2 */}
                  <Grid item xs={12}>
                  <BorderLinearProgress variant="determinate" value={Math.floor(current/total*100)} />
                  </Grid>
                </Grid>
            </Box>
        </Paper>
    )
}

ProgressBarCard.propTypes = {
    current:PropTypes.number,
    total:PropTypes.number
}
ProgressBarCard.defaultProps={
    current:18,
    total:27
}

export default ProgressBarCard

