import React from 'react';
import { Box, Grid, Avatar, Typography, Button,makeStyles } from '@material-ui/core';

const useStyles = makeStyles(({palette:theme,spacing:space})=>({
    root:{
        width:space(12),
        height:space(12),
        border:`3px solid ${theme.primary.contrastText}`,
        boxShadow:'0 0 20px 1px rgba(0, 0, 0, 0.3)'
    },
    outlined:{
        borderRadius:space(3),
        borderWidth:space(0.4),
        fontWeight:600,
        '&:hover':{
            borderWidth:space(0.3)
            
        }
    }
}))

export default function ProfileCard() {
    const classes = useStyles();

    return (
        
        <Box mt={-6}>
            <Grid
              container
              spacing={1}
              direction="column"
              justify="center"
              alignItems="center"
              alignContent="center"
              wrap="nowrap"
              
            >
              <Box mb={2}>
                  <Avatar src={'/img/gallary/child-865116_1920.jpg'} className={classes.root} />
              </Box>
              <Typography variant="h6" color="initial">
                  Jena Patterson
              </Typography>
              <Typography variant="body2" color="initial">
                  Teacher
              </Typography>
              <Box mt={2}>
                    <Button variant="outlined" color="primary" className={classes.outlined}>
                        follow
                    </Button>  
              </Box>
            </Grid>
            
        </Box>
    )
}
