import React from 'react';
import { Typography, Paper, TextField, Box, Button, makeStyles, Grid } from '@material-ui/core';
import { Link } from 'react-router-dom';

const useStyles = makeStyles(({palette,spacing})=>({
    root:{
        margin:spacing('auto',3),
        borderRadius:spacing(2)
    },
    contained:{
        borderRadius:spacing(4),
        padding:spacing(1.5,0),
        textTransform:'none'
    },
    text:{
        textTransform:'none',
        textDecoration:'underline',
        fontWeight:600,
        color:palette.primary.contrastText

    },
    bg:{
        backgroundColor:palette.primary.main,
        color:palette.primary.contrastText
    }
}))

const ForgetPasswordPage = () => {
    
    const classes = useStyles()
    return (
        <div className={classes.bg}>
            <Box pb={6}>
                <Box py={6}>
                    <Typography variant="h4" align="center">
                        ProLearn
                    </Typography>
                </Box>
                <Paper elevation={3} className={classes.root}> 
                    <Box py={8} px={3}>

                        <Typography variant="h4" align="center" gutterBottom>
                            Forgot Password
                        </Typography>
                        <Typography variant="body1" align="center">
                            Reset your password
                        </Typography>
                        <Box my={3}>
                            <form noValidate autoComplete="off">
                                <TextField
                                type='email'
                                label="EMAIL"
                                id="standard-basic"  
                                fullWidth
                                />
                            </form>
                        </Box>
                        <Box px={2}>
                            <Button component={Link} to="/changepassword" variant="contained" color="primary" className={classes.contained} fullWidth>
                                Send reset link
                            </Button>
                        </Box>
                    </Box>
                </Paper>
                <Box my={2}>
                    <Grid
                    container
                    spacing={1}
                    direction="row"
                    justify="center"
                    alignItems="center"
                    alignContent="center"
                    
                    >
                        <Button variant="text" component={Link} to="/login" className={classes.text}>
                            Back to login
                        </Button>
                    
                    </Grid>
                </Box>
            </Box>
        </div>
    )
}

export default ForgetPasswordPage