import React from 'react';
import { List, Box, Typography, Button, Grid, makeStyles } from '@material-ui/core';
import {ListViewCard} from '../Cards';

const useStyles = makeStyles(({palette,spacing})=>({
    text:{
        fontWeight:600,
        textTransform:'capitalize'
    }
}))


export default function FeedPage(props) {
    const classes = useStyles();
    return (
        <>
            {/* featured courses section */}
            <Box>
                <Box px={2} mt={2}>
                    <Grid
                    container
                    direction="row"
                    justify="space-between"
                    alignItems="flex-start"
                    >
                        <Typography variant="h5" color="initial">
                            Featured courses
                        </Typography>
                        <Button variant="text" className={classes.text} color="primary" size="large">
                            See all
                        </Button>
                    </Grid>
                </Box>
                <List>
                    {
                        [1,2,3,4,5].map((value,index)=>(
                            <ListViewCard key={index} id={index}  rating={value} />
                        ))
                    }
                </List>
            </Box>

            {/* new courses section */}
            <Box>
                <Box px={2} mt={2}>
                    <Grid
                    container
                    direction="row"
                    justify="space-between"
                    alignItems="flex-start"
                    >
                        <Typography variant="h5" color="initial">
                            New Courses
                        </Typography>
                        <Button variant="text" className={classes.text} color="primary" size="large">
                            See all
                        </Button>
                    </Grid>
                </Box>
                <List>
                    {
                        [1,2,3,4,5].map((value,index)=>(
                            <ListViewCard key={index} rating={value} />
                        ))
                    }
                </List>
            </Box>
        </>
    )
}





