import React from 'react';
import { Typography, Paper, TextField, Box, Button, makeStyles, Grid } from '@material-ui/core';
import { Link } from 'react-router-dom';

const useStyles = makeStyles(({palette,spacing})=>({
    root:{
        margin:spacing('auto',3),
        borderRadius:spacing(2)
    },
    contained:{
        borderRadius:spacing(4),
        padding:spacing(1.5,0),
        textTransform:'none'
    },
    text:{
        textTransform:'none',
        fontWeight:600,
        '&.on-primary':{
            textDecoration:'underline',
            color:palette.primary.contrastText
            
        }
        
    },
    bg:{
        backgroundColor:palette.primary.main,
        color:palette.primary.contrastText
    },
    body2:{
        color:palette.primary.contrastText
    },
    h4:{
        fontSize:spacing(4)
    }
}))

const ChangePassworPage = () => {
    
    const classes = useStyles()
    return (
        <div className={classes.bg}>
            <Box pb={6}>
                <Box py={6}>
                    <Typography variant="h4" align="center">
                        ProLearn
                    </Typography>
                </Box>
                <Paper elevation={3} className={classes.root}> 
                    <Box py={8} px={3}>

                        <Typography variant="h4" align="center" noWrap gutterBottom className={classes.h4}>
                            Change Password
                        </Typography>
                        <Typography variant="body1" align="center">
                            Insert your new password
                        </Typography>
                        <Box my={3}>
                            <form noValidate autoComplete="off">
                                <TextField
                                type='password'
                                label="NEW PASSWORD"
                                id="new-password"  
                                fullWidth
                                margin="normal"
                                />
                                <TextField
                                type='password'
                                label="CONFIRM PASSWORD"
                                id="confirm-password"  
                                fullWidth
                                />
                            </form>
                        </Box>
                        <Box px={2}>
                            <Button component={Link} to="/feed" variant="contained" color="primary" className={classes.contained} fullWidth>
                                Set new password
                            </Button>
                        </Box>
                    </Box>
                </Paper>
                <Box my={2}>
                    <Grid
                    container
                    spacing={1}
                    direction="column"
                    justify="center"
                    alignItems="center"
                    alignContent="center"
                    
                    >
                        <Typography variant="body2" className={classes.body2}>
                            Remembered your old password?
                        </Typography>
                        <Button component={Link} to="/login" variant="text" className={`${classes.text} ${"on-primary"}`}>
                            Login here
                        </Button>
                    
                    </Grid>
                </Box>
            </Box>
        </div>
    )
}

export default ChangePassworPage